<template>
  <form class="px-4 py-5" @submit.prevent="loginUser" style="max-width: 400px">
    <div v-if="!login" class="mb-3">
      <label for="inputNameUser" class="form-label">User name</label>
      <input
        minlength="3"
        maxlength="20"
        required
        v-model="userName"
        type="text"
        class="form-control"
        id="inputNameUser"
      />
      <div id="nameHelp" class="form-text">from 3 letters to 20</div>
    </div>
    <div class="mb-3">
      <label for="inputEmailUser" class="form-label">Email address</label>
      <input
        required
        v-model="emailUser"
        type="email"
        class="form-control"
        id="inputEmailUser"
        aria-describedby="emailHelp"
      />
      <div id="emailHelp" class="form-text">
        We'll never share your email with anyone else.
      </div>
    </div>
    <div class="mb-3">
      <label for="passwordUser" class="form-label">Password</label>
      <input
        minlength="6"
        required
        v-model="password"
        type="password"
        class="form-control"
        id="passwordUser"
      />
      <div id="passwordHelp" class="form-text">Must be greater than 6</div>
    </div>
    <button
      type="submit"
      class="btn btn-success d-block w-50 mx-auto mb-2"
      :disabled="profile.email"
    >
      <span v-if="!profile.email">
        <span v-if="login">login</span> <span v-else> Sign up</span>
      </span>
      <span v-else class="spinner-border text-light" role="status">
        <span class="visually-hidden">Loading...</span>
      </span>
    </button>
    <button
      type="button"
      @click="login = !login"
      class="btn btn-link text-white"
    >
      <span v-if="login">Create an account</span>
      <span v-else>login</span>
    </button>
  </form>
  <Transition name="bounce">
    <div
      v-if="error"
      class="mt-5 w-100 position-absolute top-0 d-flex align-items-top justify-content-center"
    >
      <div
        class="alert d-block mx-auto alert-danger"
        style="width: 250px"
        role="alert"
      >
        <span class="me-2"
          ><i class="fa-solid fa-bug" style="color: #8e0b0b"></i
        ></span>
        <span> Error</span>
      </div>
    </div>
  </Transition>
</template>
<script>
import auth from "@/firebase/authentiocation.js";
import { makeDatabaseUsersRepository } from "@/firebase/database-user-repository";
export default {
  name: "formUser",
  data() {
    return {
      emailUser: "",
      password: "",
      userName: "",
      login: true,
      error: "",
    };
  },
  methods: {
    dataInputReception(data, typeData) {
      if (typeData == "text") {
        this.userName = data;
      } else if (typeData == "email") {
        this.emailUser = data;
      } else {
        this.password = data;
      }
    },
    pushRount() {
      this.$router.push({ name: "Chat", query: { email: this.profile.email } });
    },
    async loginUser() {
      if (this.login) {
        this.error = !(await auth.signInUser(this.emailUser, this.password));
      } else {
        this.error = !(await auth.createUser(this.emailUser, this.password));
        if (!this.error) {
          this.$store.state.profile =
            await makeDatabaseUsersRepository().addUser({
              email: this.emailUser.toLowerCase(),
              name: this.userName.toLowerCase(),
            });
        }
      }
      this.classAnimationError();
    },
    classAnimationError() {
      if (this.error) {
        clearTimeout(this.error);
        this.error = setTimeout(() => {
          this.error = false;
        }, 1700);
      }
    },
  },
  computed: {
    profile() {
      return this.$store.state.profile;
    },
    email() {
      return this.profile.email;
    },
    listDataStore() {
      return this.$store.state.usersDetails;
    },
  },
  // TODO:
  watch: {
    email: {
      immediate: true,
      async handler() {
        if (this.email) {
          this.pushRount(this.email);
        }
      },
    },
  },
};
</script>

<style scoped>
form {
  height: 100vh;
  backdrop-filter: blur(5px);
  color: #eee;
  max-width: 300px;
}
.form-text {
  color: rgba(95, 195, 95, 0.664);
}
.errorPassowrd {
  box-shadow: 0px 0px 0px 2px red;
}
.messageError {
  color: red;
}
.form-control:focus {
  box-shadow: 0px 0px 0px 3px var(--green-color);
}
@media (max-width: 600px) {
  form {
    display: inline-block;
    height: auto;
    margin: auto;
    margin-top: 20px;
  }
}
@media (max-width: 420px) {
  form {
    margin-top: 0px;
    backdrop-filter: blur(15px);
  }
}

@keyframes error {
  0% {
    opacity: 0;
    translate: 0px 100px;
  }
  100% {
    opacity: 1;
    translate: 0px 10px;
  }
}
.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
</style>
